import { ActorRefFrom, assign, createMachine } from "xstate"

import { Account } from "@nfid/integration"

import { RPCMessage } from "frontend/features/embed/services/rpc-receiver"
import { fetchAccountsService } from "frontend/integration/identity-manager/services"
import {
  AuthorizationRequest,
  AuthorizingAppMeta,
} from "frontend/state/authorization"

export type EmbedConnectAccountMachineContext = {
  appMeta?: AuthorizingAppMeta
  authRequest: AuthorizationRequest
  accounts?: Account[]
  accountsLimit?: number
  rpcMessage?: RPCMessage
}

type Events =
  | { type: "CREATE_ACCOUNT" }
  | { type: "SELECT_ACCOUNT"; data: { accountId: Account["accountId"] } }
  | { type: "PRESENT_ACCOUNTS" }
  | { type: "CONNECTION_DETAILS" }
  | {
      type: "CONNECT_WITH_ACCOUNT"
      data: { hostname: string; accountId: string }
    }
  | { type: "CONNECT_ANONYMOUSLY" }
  | { type: "BACK" }

type Services = {
  fetchAccountsService: {
    data: Account[]
  }
}

export const EmbedConnectAccountMachine =
  /** @xstate-layout --EYYWJO3SJBELgInOrIeCucqpnquyXru+6HrmbwcC2zYaM29BqAANjqABm2G4BeGpXiht4GPebZkB2VIvj2HoIDEC4xN0XxzIiET2BEkphskUIjMOwYeBEfgDKk6I0BoEBwEYq7wemdBdk0TH0nyEJON08QQiMXGeDEYYRsGNhmBCfocsGbhScmWIKriezFKpbp0iY5h2FCMKsREw5WKxfrGdYiSTh4vQiZ5MJ+LBKbYspDDYM2WHNi5r7MdGHjdP0oQ-jxH4KEZPLvuxYmhBCYkWBYviBjF9nro5KpqhqWo6m8CFgKl6nueGMw6S4eliQ434LmYxnBtpMYQuBlVDfptVru1mbZmAub5oWxY6J17oaeG3yClpqJJJVw5jEVUrWJEUUuECYXifNSkbmR27ITeaHbdSanvW0lg-LlgLfCMH76QJoz0BB-yzG4CjxGYaRpEAA */
  createMachine(
    {
      tsTypes: {} as import("./index.typegen").Typegen0,
      schema: {
        events: {} as Events,
        context: {} as EmbedConnectAccountMachineContext,
        services: {} as Services,
      },
      id: "EmbedConnectAccountMachine",
      initial: "Start",
      states: {
        Start: {
          type: "parallel",
          states: {
            FetchAccounts: {
              invoke: {
                src: "fetchAccountsService",
                id: "fetchAccountsService",
                onDone: [
                  {
                    actions: ["assignAccounts"],
                  },
                ],
              },
            },
          },
          on: {
            CONNECTION_DETAILS: "ConnectionDetails",
          },
        },

        Error: {},

        ConnectionDetails: {
          on: {
            BACK: "Start",
          },
        },

        End: {
          type: "final",
          data: (context, event: { data: EmbedConnectAccountMachineContext }) =>
            event.data,
        },
      },
    },
    {
      services: {
        fetchAccountsService,
      },
      actions: {
        assignAccounts: assign({
          accounts: (context, event) => event.data,
        }),
      },
    },
  )

export type NFIDConnectAccountActor = ActorRefFrom<
  typeof EmbedConnectAccountMachine
>

export default EmbedConnectAccountMachine
